@use 'sass:math';

.container-fluid-row-sm {
  @include media-breakpoint-up(sm) {
    @include flex-no-wrap();
  }
}

.container-fluid-row-md {
  @include media-breakpoint-up(md) {
    @include flex-no-wrap();
  }
}

.container-fluid-row-lg {
  @include media-breakpoint-up(lg) {
    @include flex-no-wrap();
  }
}

.container-fluid-row-xl {
  @include media-breakpoint-up(xl) {
    @include flex-no-wrap();
  }
}

.container-fluid-row-xxl {
  @include media-breakpoint-up(xxl) {
    @include flex-no-wrap();
  }
}

@mixin column-width($columns, $containerWidth: 1320px) {
  .container-fluid-column.col-sm-#{$columns} {
    width: calc(((((100% / 12) * var(--columns)))) + (100vw - #{math.div($containerWidth, 2)}));
    max-width: calc(calc((100% / 12) * $columns) + (100vw - #{math.div($containerWidth, 2)}));
  }
  .container-fluid-column.col-md-#{$columns} {
    width: calc(calc((100% / 12) * $columns) + (100vw - #{math.div($containerWidth, 2)}));
    max-width: calc(calc((100% / 12) * $columns) + (100vw - #{math.div($containerWidth, 2)}));
  }
  .container-fluid-column.col-lg-#{$columns} {
    width: calc(calc((100% / 12) * $columns) + (100vw - #{math.div($containerWidth, 2)}));
    max-width: calc(calc((100% / 12) * $columns) + (100vw - #{math.div($containerWidth, 2)}));
  }
  .container-fluid-column.col-xl-#{$columns} {
    width: calc(calc((100% / 12) * $columns) + (100vw - #{math.div($containerWidth, 2)}));
    max-width: calc(calc((100% / 12) * $columns) + (100vw - #{math.div($containerWidth, 2)}));
  }
  .container-fluid-column.col-xxl-#{$columns} {
    width: calc(calc((100% / 12) * $columns) + (100vw - #{math.div($containerWidth, 2)}));
    max-width: calc(calc((100% / 12) * $columns) + (100vw - #{math.div($containerWidth, 2)}));
  }
}

.container-fluid-column {
  position: relative;
  --col-width: 100%;


  @for $i from 1 through 12 {
    &.col-#{$i},
    &.col-xs-#{$i},
    &.col-sm-#{$i},
    &.col-md-#{$i},
    &.col-lg-#{$i},
    &.col-xl-#{$i},
    &.col-xxl-#{$i} {
      --sidebar-width: calc((100vw - var(--container-width)) / 2);
      width: calc(var(--col-width) + var(--sidebar-width));
      min-width: calc(var(--col-width) + var(--sidebar-width));
      max-width: calc(var(--col-width) + var(--sidebar-width));
    }
    @include media-breakpoint-up(sm) {
      &.col-sm-#{$i} {
        --col-width: #{$i * 8.33334}%;
      }
    }
    @include media-breakpoint-up(md) {
      &.col-md-#{$i} {
        --col-width: #{$i * 8.33334}%;
      }
    }
    @include media-breakpoint-up(lg) {
      &.col-lg-#{$i} {
        --col-width: #{$i * 8.33334}%;
      }
    }
    @include media-breakpoint-up(xl) {
      &.col-xl-#{$i} {
        --col-width: #{$i * 8.33334}%;
      }
    }
    @include media-breakpoint-up(xxl) {
      &.col-xxl-#{$i} {
        --col-width: #{$i * 8.33334}%;
      }
    }
  }
}
