@import "em-to-px";

::-moz-selection{
  background: rgba(var(--bs-primary-rgb), .25);
}

::selection {
  background: rgba(var(--bs-primary-rgb), .25);
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  pointer-events: none;
}

.overflow-hidden {
  overflow: hidden !important;
}

.fw-100 {
  font-weight: 100 !important;
}

.fw-200 {
  font-weight: 200 !important;
}

.fw-300 {
  font-weight: 300 !important;
}

.fw-400 {
  font-weight: 400 !important;
}

.fw-500 {
  font-weight: 500 !important;
}

.fw-600 {
  font-weight: 600 !important;
}

.fw-700 {
  font-weight: 700 !important;
}

.fw-800 {
  font-weight: 800 !important;
}

.fw-900 {
  font-weight: 900 !important;
}

.text-transform-none {
  text-transform: none !important;
}

.mw-fit-content {
  max-width: fit-content !important;
}

.mh-100vh {
  min-height: 100vh;
}

.btn {
  text-transform: uppercase;
  .icon {
    vertical-align: middle;
    margin-right: rem(8);
  }
}

h1, .h1 {
  font-size: rem(40);
  margin-bottom: rem(16);
  @include media-breakpoint-up(md) {
    font-size: rem(50);
    margin-bottom: rem(20);
  }
  @include media-breakpoint-up(lg) {
    font-size: rem(60);
    margin-bottom: rem(30);
  }
}

h2, .h2 {
  font-size: rem(36);
  margin-bottom: rem(16);
  @include media-breakpoint-up(md) {
    font-size: rem(40);
    margin-bottom: rem(20);
  }
  @include media-breakpoint-up(lg) {
    font-size: rem(50);
    margin-bottom: rem(30);
  }
}

h3, .h3 {
  font-size: rem(28);
  margin-bottom: rem(16);
  @include media-breakpoint-up(md) {
    font-size: rem(36);
    margin-bottom: rem(20);
  }
  @include media-breakpoint-up(lg) {
    font-size: rem(40);
    margin-bottom: rem(30);
  }
}

h4, .h4 {
  font-size: rem(24);
  margin-bottom: rem(16);
  @include media-breakpoint-up(md) {
    font-size: rem(28);
    margin-bottom: rem(20);
  }
  @include media-breakpoint-up(lg) {
    font-size: rem(36);
    margin-bottom: rem(30);
  }
}

h5, .h5 {
  font-size: rem(22);
  margin-bottom: rem(16);
  @include media-breakpoint-up(md) {
    font-size: rem(24);
    margin-bottom: rem(20);
  }
  @include media-breakpoint-up(lg) {
    font-size: rem(28);
    margin-bottom: rem(30);
  }
}

h6, .h6 {
  font-size: rem(20);
  margin-bottom: rem(16);
  @include media-breakpoint-up(md) {
    font-size: rem(22);
    margin-bottom: rem(20);
  }
  @include media-breakpoint-up(lg) {
    font-size: rem(24);
    margin-bottom: rem(30);
  }
}

.skip-link {
  display: inline-block;;
  padding: rem(4);
  margin: rem(4);
  color: var(--bs-body-color);
  position: relative;
  z-index: 101;
  @include visually-hidden-focusable();
}

.text-gradient {
  background: linear-gradient(90deg, var(--bs-primary) 0%, var(--bs-secondary) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.text-keyword {
  color: var(--bs-body-bg);                  /* Text color */
  text-shadow:
    2px 2px 0 var(--bs-body-color),             /* Right and down */
    -2px -2px 0 var(--bs-body-color),           /* Left and up */
    -2px 2px 0 var(--bs-body-color),            /* Left and down */
    2px -2px 0 var(--bs-body-color);            /* Right and up */
}

.main-section {
  position: relative;
  overflow: hidden;
  @include section-padding();
}

.bg-dot-pattern {
  position: relative;
  overflow: hidden;
  &:before {
    content: '';
    background: transparent url("../images/gradient.png") no-repeat;
    background-size: 100% 100%;
    width: rem(450);
    height: rem(650);
    max-width: 100%;
    max-height: 100%;
    position: absolute;
  }
  &.position-right-1 {
    &:before {
      left: 100%;
      top: 50%;
      transform: translate(-60%, -50%);
    }
  }
  &.position-right-2 {
    &:before {
      left: 100%;
      top: 50%;
      height: rem(800);
      width: rem(400);
      max-height: 100%;
      max-width: 100%;
      transform: translate(-60%, -60%);
    }
  }
  &.position-right-3 {
    &:before {
      left: 100%;
      transform: translate(-75%, 0);
    }
  }
  &.position-left-1 {
    &:before {
      left: 0;
      top: 50%;
      transform: translate(-50%, -50%) rotate(-20deg);
      opacity: .75;
    }
  }
  &.position-left-2 {
    &:before {
      left: 0;
      top: 50%;
      height: rem(800);
      width: rem(700);
      max-height: 100%;
      max-width: 100%;
      transform: translate(-50%, -50%);
      opacity: .75;
      object-fit: contain;
    }
  }
  &.position-left-3 {
    &:before {
      left: 0%;
      transform: translate(-75%, 0);
    }
  }
}

.inline-code {
  background: var(--bs-black);
  padding: rem(2) rem(4);
  border-radius: rem(4);
  font-size: 90%;
  font-family: var(--bs-code-font);
  color: var(--bs-white);
}

hr {
  opacity: 1;
}

